// src/services/authService.js

import { reactive } from 'vue';

const USERS_KEY = 'users';

// Initialize users with test data or load from localStorage
let users = JSON.parse(localStorage.getItem(USERS_KEY)) || [
  { email: 'testuser1@example.com', password: 'password123' },
  { email: 'testuser2@example.com', password: 'securepass456' },
  { email: 'testuser3@example.com', password: 'mypassword789' },
];

// Save users to localStorage
const saveUsers = () => {
  localStorage.setItem(USERS_KEY, JSON.stringify(users));
};

// Reactive state for authentication
export const authState = reactive({
  isLoggedIn: !!localStorage.getItem('isLoggedIn'),
  currentUser: JSON.parse(localStorage.getItem('currentUser')),
});

export const authService = {
  // Register a new user
  register(email, password) {
    if (users.some((user) => user.email === email)) {
      throw new Error('This email is already registered.');
    }
    const newUser = { email, password };
    users.push(newUser);
    saveUsers();
  },

  // Validate user credentials and login
  login(email, password) {
    const user = users.find((user) => user.email === email && user.password === password);
    if (!user) {
      throw new Error('Invalid email or password.');
    }
    localStorage.setItem('isLoggedIn', true);
    localStorage.setItem('currentUser', JSON.stringify(user));
    authState.isLoggedIn = true;
    authState.currentUser = user;
  },

  // Logout the current user
  logout() {
    localStorage.removeItem('isLoggedIn');
    localStorage.removeItem('currentUser');
    authState.isLoggedIn = false;
    authState.currentUser = null;
  },

  // Get all users (for debugging or display)
  getAllUsers() {
    return users;
  },
};
