<template>
  <div class="add-post-page">
    <h1>Add Post</h1>
    <form @submit.prevent="addPost">
      <textarea v-model="body" placeholder="Write your post" required></textarea>
      <button type="submit" class="btn-primary">Add Post</button>
    </form>
    <p v-if="error" class="error">{{ error }}</p>
    <p v-if="success" class="success">{{ success }}</p>
  </div>
</template>

<script>
import { addPost } from '@/services/api';

export default {
  data() {
    return {
      body: '',
      error: null,
      success: null,
    };
  },
  methods: {
    async addPost() {
      try {
        const token = localStorage.getItem('token');
        if (!token) throw new Error('User not logged in');
        await addPost({ body: this.body, token });
        this.success = 'Post added successfully!';
        this.body = '';
        this.error = null;
      } catch (err) {
        this.error = 'Failed to add post.';
        this.success = null;
      }
    },
  },
};
</script>

<style scoped>
.add-post-page {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}
.success {
  color: green;
}
.error {
  color: red;
}
</style>
