<template>
    <div class="contact-page">
      <div class="contact-header">
        <h1>Contact Us</h1>
        <p>We'd love to hear from you! Reach out using the form below or the information provided.</p>
      </div>
      <div class="contact-info">
        <div class="info-item">
          <strong>Email:</strong>
          <p>support@ourblog.com</p>
        </div>
        <div class="info-item">
          <strong>Phone:</strong>
          <p>+1 (800) 555-1234</p>
        </div>
        <div class="info-item">
          <strong>Address:</strong>
          <p>123 Blog Street, Blog City, BL 56789</p>
        </div>
      </div>
      <h2>Send Us a Message</h2>
      <form @submit.prevent="submitContactForm" class="contact-form">
        <div class="form-group">
          <label for="name">Your Name:</label>
          <input id="name" v-model="name" type="text" placeholder="Enter your name" required />
        </div>
        <div class="form-group">
          <label for="email">Your Email:</label>
          <input id="email" v-model="email" type="email" placeholder="Enter your email" required />
        </div>
        <div class="form-group">
          <label for="message">Your Message:</label>
          <textarea id="message" v-model="message" placeholder="Write your message" required></textarea>
        </div>
        <button type="submit" class="btn-primary">Send Message</button>
      </form>
      <p v-if="success" class="success">{{ success }}</p>
      <p v-if="error" class="error">{{ error }}</p>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        name: '',
        email: '',
        message: '',
        success: null,
        error: null,
      };
    },
    methods: {
      async submitContactForm() {
        try {
          if (!this.name || !this.email || !this.message) {
            throw new Error('All fields are required.');
          }
          this.success = 'Your message has been sent successfully!';
          this.error = null;
          this.name = '';
          this.email = '';
          this.message = '';
        } catch (err) {
          this.success = null;
          this.error = 'Failed to send your message. Please try again.';
        }
      },
    },
  };
  </script>
  
