<template>
  <div class="main-page">
    <h1>Blog Posts</h1>

    <div class="posts" v-if="posts.length > 0">
      <Post
        v-for="post in posts"
        :key="post.id"
        :post="post"
        @click.native="viewPost(post.id)"
      />
    </div>
    <div v-else>
      <p>No posts available. Add some posts to get started!</p>
    </div>
    <div class="actions">
      <button @click="$router.push('/add-post')" class="btn-primary">Add Post</button>
      <button @click="deleteAllPosts" class="btn-danger">Delete All Posts</button>
      <button @click="logout" class="btn-secondary">Logout</button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import { authService } from '@/services/authService';
import Post from '../components/Post.vue';

export default {
  components: {
    Post,
  },
  computed: {
    ...mapGetters(['allPosts']),
    posts() {
      return this.allPosts;
    },
  },
  methods: {
    ...mapActions(['loadPosts', 'deleteAllPosts']),
    logout() {
      authService.logout();
      this.$router.push('/login');
    },
    viewPost(id) {
      this.$router.push(`/post/${id}`);
    },
  },
  created() {
    if (!authService.isLoggedIn()) {
      this.$router.push('/login');
    } else {
      this.loadPosts();
    }
  },
};
</script>

